.information-cube {
	padding-top: 32px;
	margin-bottom: 96px;

	&__list {
		display: flex;
		justify-content: center;
		gap: 24px;

		@media screen and (max-width: 1200px) {
			display: grid;
			grid-template-columns: repeat(2, 1fr);
			justify-items: center;
		}

		@media screen and (max-width: 678px) {
			grid-template-columns: 1fr;
		}
	}

	&__item {
		width: 100%;
		max-width: 25%;
		display: flex;
		flex-direction: column;
		align-items: center;
		gap: 12px;
		border-radius: 12px;
		padding: 24px;

		@media screen and (max-width: 1200px) {
			max-width: 80%;
		}

    @media screen and (max-width: 678px) {
      width: fit-content;
		}

		&:nth-child(2n + 1) {
			box-shadow: 0px 4px 12px 0px #00000070;
		}
	}

	&__icon {
		width: 36px;
		height: 36px;
		fill: #333;
	}

	&__title {
		font-size: 20px;

		@media screen and (max-width: 1320px) {
			font-size: 18px;
		}
	}

	&__description {
		color: #444;
		font-style: 15px;
		line-height: 2;
		text-align: center;

		@media screen and (max-width: 1320px) {
			font-size: 14px;
      line-height: 1.8;
		}
	}

	&__links {
		display: flex;
		flex-direction: column;
		align-items: center;
		gap: 6px;
	}

	&__link {
		font-size: 18px;
		color: #0170b9;
		transition: color 0.3s ease-in-out;

		@media screen and (max-width: 1320px) {
			font-size: 16px;
		}

		&:hover {
			color: #004a7c;
		}
	}
}
