.directions-template {
	display: flex;
	flex-direction: column;
	align-items: center;
	gap: 12px;
	padding-top: 134px;
	margin-bottom: 32px;

	&__content {
		display: flex;
		gap: 24px;
		border-radius: 12px;
		background-color: #1c75bc;
		overflow: hidden;
		box-shadow: rgba(0, 0, 0, 0.07) 0px 1px 1px, rgba(0, 0, 0, 0.07) 0px 2px 2px, rgba(0, 0, 0, 0.07) 0px 4px 4px,
			rgba(0, 0, 0, 0.07) 0px 8px 8px, rgba(0, 0, 0, 0.07) 0px 16px 16px;

		@media screen and (max-width: 1160px) {
			width: 80%;
			flex-direction: column;
			gap: 12px;
		}

		@media screen and (max-width: 678px) {
			width: 100%;
		}
	}

	&__img {
		max-width: 600px;
		width: 100%;
		height: auto;
		object-fit: cover;
		object-position: center;

		@media screen and (max-width: 1160px) {
			max-width: 100%;
		}
	}

	&__wrapper {
		display: flex;
		flex-direction: column;
		padding: 12px 24px 24px 0;
		gap: 6px;

		@media screen and (max-width: 1160px) {
			padding: 0 24px 24px 24px;
		}
	}

	&__description {
		& > p {
			color: #fff;
			font-size: 15px;
			line-height: 1.5;
			text-indent: 24px;
			text-align: justify;

			@media screen and (max-width: 678px) {
				font-size: 14px;
			}
		}
	}

	&__focused {
		width: fit-content;
		color: #fff;
		border-bottom: 2px solid #fff;
		margin-bottom: 6px;
	}

	&__advantages {
		display: flex;
		flex-direction: column;
		gap: 6px;
		padding-inline-start: 12px;

		& > p {
			font-size: 14px;
			color: #fff;
			line-height: 1.5;

			@media screen and (max-width: 678px) {
				font-size: 13px;
			}
		}
	}

	&__teachers {
		color: #fff;
		margin-bottom: 12px;

		&-text {
			width: fit-content;
			border-bottom: 2px solid #fff;

			@media screen and (max-width: 678px) {
				font-size: 15px;
			}
		}

		@media screen and (max-width: 678px) {
			margin-bottom: 6px;
		}
	}

	&__list {
		margin-top: auto;
		display: flex;
		flex-direction: column;
		gap: 6px;
	}

	&__programs {
		font-size: 18px;
		font-weight: 600;
		color: #fff;

		@media screen and (max-width: 678px) {
			font-size: 17px;
		}
	}

	&__link {
		font-size: 14px;
		color: #fff;
		padding-bottom: 2px;
		border-bottom: 1px solid transparent;
		margin-inline-start: 12px;
		transition: border-color 0.3s ease-in-out;

		@media (hover: hover) {
			&:hover {
				border-color: #fff;
			}
		}

		&:active {
			border-color: #fff;
		}
	}
}
