.documents {
	display: flex;
	flex-direction: column;
	align-items: center;
	padding-top: 134px;
	margin-bottom: 32px;

	&__list {
		width: 80%;
		display: flex;
		flex-direction: column;
	}

	&__item {
		padding-top: 24px;
	}

	&__wrapper {
		display: flex;
		flex-direction: column;
		gap: 12px;
		background-color: #1c75bc;
		padding: 24px;
		border-radius: 12px;
	}

	&__title {
		color: #fff;
		font-size: 22px;
		font-weight: 600;
		text-align: center;
		margin-bottom: 12px;

		@media screen and (max-width: 998px) {
			font-size: 18px;
		}
	}

	&__link {
		width: fit-content;
		display: block;
		color: #d4d3d3;
		font-weight: 400;
		padding-bottom: 5px;
		transition: color 0.3s ease-in-out;
		line-height: 1.5;

		@media screen and (max-width: 998px) {
			font-size: 14px;
		}

		&:active {
			color: #fff;
			text-decoration: underline;
		}

		@media (hover: hover) {
			&:hover {
				color: #fff;
				text-decoration: underline;
			}
		}
	}
}
