.news {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 36px;
  padding-top: 134px;
  margin-bottom: 36px;

  &__list {
    display: grid;
    grid-template-columns: repeat(3, minmax(250px, 350px));
    gap: 24px;

    @media screen and (max-width: 1160px) {
      grid-template-columns: repeat(2, minmax(250px, 350px));
    }

    @media screen and (max-width: 678px) {
      grid-template-columns: 1fr;
      justify-items: center;
    }
  }

  &__item {
    display: flex;
    flex-direction: column;
    border-radius: 12px;
    box-shadow: 0px 20px 30px 0px rgb(0 0 0 / 15%);
    overflow: hidden;

    @media screen and (max-width: 678px) {
      width: 90%;
    }

    &-content {
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      gap: 24px;
      padding: 12px;
    }

    &-info {
      display: flex;
      flex-direction: column;
      gap: 12px;
    }

    &-img {
      display: block;
      width: 100%;
      height: auto;
      object-fit: cover;
      object-position: center;
    }

    &-date {
      font-size: 14px;
      font-weight: 500;
      line-height: 23px;
      color: #444;
    }

    &-description {
      font-size: 14px;
      color: #444;
      line-height: 1.9;
    }

    &-link {
      width: fit-content;
      padding: 12px 24px;
      border: 1px solid #1c75bc;
      color: #1c75bc;
      border-radius: 16px;
      font-size: 18px;
      transition: color 0.3s ease-in-out, background-color 0.3s ease-in-out;
      -webkit-tap-highlight-color: rgba(0, 0, 0, 0);

      @media (hover: hover) {
        &:hover {
          color: #fff;
          background-color: #1c75bc;
        }
      }

      &:active {
        color: #fff;
        background-color: #1c75bc;
      }
    }
  }
}
