.navigation {
	display: flex;
	align-items: center;
	justify-content: space-between;
	gap: 48px;
	padding-inline: 20px;

	@media screen and (max-width: 1160px) {
		gap: 32px;
	}

	&__menu {
		display: flex;
		align-items: center;
		gap: 32px;

		@media screen and (max-width: 1160px) {
			gap: 16px;
		}

		@media screen and (max-width: 998px) {
			display: none;
		}

		&-item {
			padding-block: 5px 3px;
		}

		&-link {
			display: block;
			height: 100%;
			color: rgb(255, 255, 255);
			font-weight: 500;
			font-size: 18px;
			line-height: 1.8;
			border-bottom: 1px solid transparent;
			transition: 0.3s ease border-color, 0.3s ease color;

			@media screen and (max-width: 1160px) {
				font-size: 16px;
			}

			@media screen and (max-width: 998px) {
				font-size: 22px;
			}

			@media (hover: hover) {
				&:hover {
					&:not(.navigation__menu-link--active) {
						color: rgba(255, 255, 255, 0.6);
						border-color: rgba(255, 255, 255, 0.6);
					}
				}
			}

			&:active {
				&:not(.navigation__menu-link--active) {
					color: rgba(255, 255, 255, 0.6);
					border-color: rgba(255, 255, 255, 0.6);
				}
			}

			&--active {
				border-color: rgba(255, 255, 255, 0.3);
			}
		}
	}

	&__right {
		display: flex;
		align-items: center;
		gap: 32px;

		@media screen and (max-width: 1160px) {
			gap: 16px;
		}
	}

	&__links {
		display: flex;
		align-items: center;
		gap: 16px;

		@media screen and (max-width: 998px) {
			display: none;
		}
	}

	&__link {
		@media (hover: hover) {
			&:hover {
				.navigation__icon {
					fill: rgba(255, 255, 255, 0.6);
				}
			}
		}

		&:active {
			.navigation__icon {
				fill: rgba(255, 255, 255, 0.6);
			}
		}
	}

	&__icon {
		width: 24px;
		height: 24px;
		fill: rgb(255, 255, 255);
		transition: fill 0.3s ease-in-out;
	}
}

.hamburger {
	display: none;
	position: relative;
	z-index: 10;
	width: 35px;
	height: 30px;
	margin: 10px 10px;
	position: relative;
	cursor: pointer;

	@media screen and (max-width: 998px) {
		display: inline-block;
	}

	& > span {
		background-color: #fff;
		position: absolute;
		border-radius: 2px;
		transition: 0.3s cubic-bezier(0.8, 0.5, 0.2, 1.4);
		width: 100%;
		height: 4px;
		transition-duration: 500ms;

		&:nth-child(1) {
			top: 0px;
			left: 0px;
		}

		&:nth-child(2) {
			top: 13px;
			left: 0px;
			opacity: 1;
		}

		&:nth-child(3) {
			bottom: 0px;
			left: 0px;
		}
	}

	&--open {
		& > span:nth-child(1) {
			transform: rotate(45deg);
			top: 13px;
		}

		& > span:nth-child(2) {
			opacity: 0;
		}

		& > span:nth-child(3) {
			transform: rotate(-45deg);
			top: 13px;
		}
	}

	&:not(.hamburger--open) {
		@media (hover: hover) {
			&:hover {
				& > span {
					&:nth-child(1) {
						transform: rotate(-3deg) scaleY(1.1);
					}
					&:nth-child(2) {
						transform: rotate(3deg) scaleY(1.1);
					}
					&:nth-child(3) {
						transform: rotate(-4deg) scaleY(1.1);
					}
				}
			}
		}
	}
}

.mobile-navigation {
	position: fixed;
	display: flex;
	align-items: center;
	justify-content: center;
	z-index: 5;
	width: 100%;
	height: 100vh;
	left: 0;
	top: 0;
	background-color: rgba(0, 0, 0, 0.9);
	opacity: 0;
	transform: translateX(100%);
	transition: transform 0.5s ease-in-out, opacity 0.7s ease-in-out;

	&--open {
		transform: translateX(0);
		opacity: 1;
	}

	&__menu {
		display: flex;
		flex-direction: column;
		align-items: center;
	}
}
