.logo {
	display: flex;
	align-items: center;
	gap: 8px;

	&__cube {
		position: relative;
		height: 86px;
		padding-inline-end: 6px;

		@media screen and (max-width: 1160px) {
			height: 64px;
		}

		&::after {
			content: "";
			position: absolute;
			width: 1.5px;
			height: 60%;
			right: 0;
			top: 50%;
			background-color: #fff;
			transform: translateY(-50%);
		}
	}

	&__icon {
		width: 86px;
		height: 86px;
		fill: #fff;
		transition: fill 0.3s ease-in-out;

		@media screen and (max-width: 1160px) {
			width: 64px;
			height: 64px;
		}

		&:hover {
			fill: rgba(255, 255, 255, 0.6);
		}
	}

	&__text {
		font-size: 14px;
		font-weight: 600;
		line-height: 1.2;
		color: #fff;
		text-wrap: nowrap;
		transition: color 0.3s ease-in-out;

		@media screen and (max-width: 1160px) {
			font-size: 12px;
		}

		&:hover {
			color: rgba(255, 255, 255, 0.6);
		}
	}
}
