.fqa {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 32px;
  padding-top: 24px;
  margin-bottom: 32px;

  &__title {
    position: relative;
    font-size: 27px;
    text-align: center;
    padding-bottom: 16px;

    &::after {
      content: '';
      position: absolute;
      width: 50%;
      height: 2px;
      background-color: #0170b9;
      left: 50%;
      bottom: 0;
      transform: translateX(-50%);
    }
  }

  &-accordion {
    max-width: 70%;
    display: flex;
    flex-direction: column;
    gap: 12px;

    @media screen and (max-width: 998px) {
      max-width: 100%;
    }

    &__item {
      border: 1px solid #d2d2d2;
      border-radius: 6px;
      overflow: hidden;

      .icon-minus {
        display: none;
      }

      &--active {
        .fqa-accordion__item-btn {
          background-color: #b5d9f5;
        }

        .fqa-accordion__item-title {
          color: #024069;
        }

        .fqa-accordion__item-icon {
          fill: #024069;
        }

        .fqa-accordion__item-body {
          max-height: 500px;
          padding-block: 12px;
          overflow: visible;
        }

        .fqa-accordion__item-description {
          opacity: 1;
        }

        .icon-plus {
          display: none;
        }

        .icon-minus {
          display: block;
        }
      }

      &-btn {
        position: relative;
        width: 100%;
        display: flex;
        align-items: center;
        gap: 6px;
        background-color: #e5e9ec;
        text-align: left;
        padding: 12px;
        transition: background-color 0.5s ease-in-out;
        cursor: pointer;

        @media (hover: hover) {
          &:hover {
            background-color: #b5d9f5;

            .fqa-accordion__item-title {
              color: #024069;
            }

            .fqa-accordion__item-icon {
              fill: #024069;
            }
          }
        }
      }

      &-icon {
        width: 14px;
        height: 14px;
        transition: fill 0.5s ease-in-out;
      }

      &-title {
        color: #444;
        transition: color 0.5s ease-in-out;
      }

      &-body {
        max-height: 0;
        padding-inline: 12px;
        overflow: hidden;
        transition: max-height 0.5s ease-in-out, padding 0.5s ease-in-out,
          overflow 0.5s ease-in-out;
      }

      &-description {
        color: #444;
        line-height: 1.8;
        opacity: 0;
        transition: opacity 0.5s ease-in-out;
      }
    }
  }
}
