.banner {
	position: relative;
	width: 100%;
	height: 100dvh;
	margin-bottom: 64px;
	background-color: #394a7e;

	&__logo {
		display: flex;
		align-items: center;
		gap: 64px;
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);

		@media screen and (max-width: 1160px) {
			gap: 48px;
		}

		@media screen and (max-width: 998px) {
			gap: 32px;
		}

		@media screen and (max-width: 678px) {
			flex-direction: column;
			gap: 0;
		}

		&-icon {
			width: 340px;
			height: 340px;
			fill: #dcdada;

			@media screen and (max-width: 1160px) {
				width: 280px;
				height: 280px;
			}

			@media screen and (max-width: 998px) {
				width: 200px;
				height: 200px;
			}

			@media screen and (max-width: 678px) {
				width: 160px;
				height: 160px;
			}
		}
	}

	&__title {
		font-size: 38px;
		text-wrap: nowrap;
		color: #dcdada;
		text-transform: uppercase;

		@media screen and (max-width: 1160px) {
			font-size: 30px;
		}

		@media screen and (max-width: 998px) {
			font-size: 24px;
		}

		@media screen and (max-width: 678px) {
			font-size: 18px;
			text-align: center;
		}
	}

