	&__scroll {
		position: absolute;
		background-color: transparent;
		z-index: 1;
		left: 50%;
		bottom: 0;
		transform: translateX(-50%);
		cursor: pointer;

		&-icon {
			width: 64px;
			height: 64px;
			fill: none;
			stroke: rgba(255, 255, 255);
			animation: blink 2s infinite;
		}
	}

	@keyframes blink {
		0% {
			opacity: 1;
		}
		50% {
			opacity: 0.5;
		}
		100% {
			opacity: 1;
		}
	}
}
