.footer {
	background-color: #1c75bc;

	&__content {
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding-block: 24px;
		gap: 24px;

		@media screen and (max-width: 998px) {
			display: flex;
			justify-content: center;
			flex-wrap: wrap;
			justify-content: center;
		}
	}

	&__description {
		max-width: 300px;
		display: flex;
		flex-direction: column;

		@media screen and (max-width: 1160px) {
			max-width: 250px;
		}

		@media screen and (max-width: 998px) {
			max-width: 300px;
		}
	}

	&__title {
		font-size: 24px;
		font-weight: 700;
		color: #fff;

		@media screen and (max-width: 1160px) {
			font-size: 20px;
		}

		@media screen and (max-width: 998px) {
			font-size: 24px;
		}
	}

	&__text {
		font-weight: 400;
		line-height: 1.5;
		color: #fff;

		@media screen and (max-width: 1160px) {
			font-size: 13px;
		}

		@media screen and (max-width: 998px) {
			font-size: 16px;
		}
	}

	&__links {
		display: flex;
		flex-direction: column;
		gap: 12px;
		align-items: center;

		@media screen and (max-width: 1160px) {
			gap: 6px;
		}

		@media screen and (max-width: 998px) {
			gap: 12px;
			order: 3;
		}
	}

	&__list {
		display: flex;
		align-items: center;
		gap: 12px;

		@media screen and (max-width: 1160px) {
			gap: 6px;
		}

		@media screen and (max-width: 998px) {
			gap: 12px;
		}
	}

	&__item {
		padding: 0 12px;

		@media screen and (max-width: 1160px) {
			padding: 0 6px;
		}
	}

	&__link {
		position: relative;
		color: #fff;
		font-size: 15px;
		transition: color 0.3s ease-in-out;

		@media screen and (max-width: 1160px) {
			font-size: 13px;
		}

		@media screen and (max-width: 998px) {
			font-size: 16px;
		}

		@media (hover: hover) {
			&:hover {
				color: rgba(255, 255, 255, 0.6);
			}
		}

		&:active {
			color: rgba(255, 255, 255, 0.6);
		}
	}

	&__map {
		display: flex;
		flex-direction: column;
		align-items: center;
		gap: 6px;
		height: 200px;
		border-radius: 3px;
		overflow: hidden;

		&-text {
			font-size: 13px;
			color: #fff;
		}
	}
}
