.events {
	display: flex;
	flex-direction: column;
	align-items: center;
	gap: 24px;
	padding-top: 134px;
	margin-bottom: 24px;

	&__list {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		gap: 24px;

		@media screen and (max-width: 998px) {
			grid-template-columns: repeat(1, minmax(350px, 700px));
		}
	}

	&__item {
		display: flex;
		flex-direction: column;
		gap: 12px;
		border-radius: 12px;
		overflow: hidden;
		box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;
	}

	&__img {
		width: 100%;
		height: auto;
		object-fit: cover;
		object-position: center;
	}

	&__content {
		padding: 0 24px 24px 24px;

		@media screen and (max-width: 998px) {
			padding: 0 12px 12px 12px;
		}
	}

	&__title {
		font-size: 22px;
		color: #1c75bc;
		text-align: center;
		margin-bottom: 6px;
	}

	&__date {
		display: block;
		color: #1c75bc;
		text-align: right;
		margin-bottom: 6px;
	}

	&__info {
		color: #444;
		line-height: 1.5;
		text-align: justify;
	}
}
