.news-template {
  display: flex;
  gap: 32px;
  padding: 134px 24px 24px 24px;
  margin-bottom: 32px;
  border-radius: 12px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;

  @media screen and (max-width: 1160px) {
    gap: 24px;
  }

  @media screen and (max-width: 998px) {
    flex-direction: column;
    gap: 12px;
  }

  &__left {
    display: flex;
    flex-direction: column;
    gap: 6px;
  }

  &__img {
    align-self: center;
    width: 550px;
    height: auto;
    object-fit: cover;
    object-position: center;
    border-radius: 6px;

    @media screen and (max-width: 1160px) {
      width: 400px;
    }

    @media screen and (max-width: 998px) {
      width: 100%;
    }
  }

  &__date {
    color: #1c75bc;
    align-self: flex-end;
  }

  &__right {
    height: fit-content;
    display: flex;
    flex-direction: column;
    gap: 24px;
    padding: 12px 24px;
    background-color: #1c75bc;
    border-radius: 6px;

    @media screen and (max-width: 998px) {
      gap: 12px;
    }
  }

  &__title {
    font-size: 20px;
    font-weight: 600;
    color: #fff;
    text-align: center;
    line-height: 1.2;
    padding-inline: 12px;

    @media screen and (max-width: 998px) {
      font-size: 18px;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    gap: 12px;

    @media screen and (max-width: 998px) {
      gap: 6px;
    }
  }

  &__description {
    color: #f0eded;
    text-align: justify;
    line-height: 1.5;
    text-indent: 24px;

    @media screen and (max-width: 998px) {
      font-size: 15px;
    }
  }
}
