.contacts {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 24px;
  padding-top: 134px;
  margin-bottom: 32px;

  &__wrapper {
    display: flex;
    align-items: center;
    gap: 32px;

    @media screen and (max-width: 998px) {
      flex-direction: column;
    }
  }

  &__info {
    display: flex;
    flex-direction: column;
    gap: 24px;

    @media screen and (max-width: 1160px) {
      gap: 12px;
    }

    &-row {
      min-width: 360px;
      display: flex;
      align-items: center;
      gap: 12px;
      padding-bottom: 12px;
      border-bottom: 1px dashed #444;

      @media screen and (max-width: 998px) {
        min-width: 460px;
        gap: 6px;
        padding-bottom: 6px;
      }

      @media screen and (max-width: 678px) {
        min-width: 300px;
      }
    }

    &-img {
      display: block;
      width: 43px;
      height: auto;
      object-fit: cover;
      object-position: center;

      @media screen and (max-width: 998px) {
        width: 32px;
      }
    }

    &-description {
      display: flex;
      flex-direction: column;
      gap: 6px;

      @media screen and (max-width: 998px) {
        gap: 2px;
      }
    }

    &-title,
    &-text {
      font-size: 12px;
      color: #333;
    }

    &-text {
      font-size: 15px;

      @media screen and (max-width: 998px) {
        font-size: 13px;
      }
    }

    &-link {
      font-size: 15px;
      color: #0170b9;
      transition: color 0.3s ease-in-out;

      @media screen and (max-width: 998px) {
        font-size: 13px;
      }

      @media (hover: hover) {
        &:hover {
          color: #01385e;
        }
      }
    }
  }

  &__map {
    border-radius: 12px;
    overflow: hidden;

    & > iframe {
      width: 700px;
      height: 400px;

      @media screen and (max-width: 1160px) {
        width: 500px;
        height: 300px;
      }

      @media screen and (max-width: 678px) {
        width: 350px;
        height: 200px;
      }
    }
  }
}
