.contacts-us {
	display: flex;
	justify-content: center;
	padding-top: 134px;
	margin-bottom: 32px;

	&__form {
		display: flex;
		flex-direction: column;
		align-items: center;
		gap: 12px;
		background-color: #1c75bc;
		border-radius: 12px;
		padding: 24px;
		box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;
	}

	&__title {
		font-size: 22px;
		color: #fff;
		margin-bottom: 12px;
		text-align: center;
	}

	&__field {
		position: relative;
		min-width: 350px;
		z-index: 2;
		transition: margin 0.3s ease-in-out;

		&.invalid {
			margin-bottom: 18px;
		}

		&:not(.invalid) {
			& > .contacts-us__input:focus ~ .contacts-us__label,
			& > .contacts-us__input:not(:placeholder-shown) ~ .contacts-us__label {
				color: #d4d3d3;
			}
		}

		&.invalid {
			& > .contacts-us__input {
				border-color: rgb(159 0 0);
			}

			& > .contacts-us__label {
				color: rgb(159 0 0);
			}
		}
	}

	&__label {
		position: absolute;
		left: 10px;
		top: 50%;
		font-size: 14px;
		color: #fff;
		padding-inline: 5px;
		background-color: #1c75bc;
		transform: translateY(-50%);
		transition: top 0.3s ease-in-out, font-size 0.3s ease-in-out, color 0.3s ease-in-out;
	}

	&__input {
		width: 100%;
		padding: 6px 12px;
		border-radius: 12px;
		font-size: 14px;
		line-height: 1.8;
		color: #fff;
		background-color: transparent;
		outline: none;
		border: 2px solid #fff;

		&:focus ~ .contacts-us__label,
		&:not(:placeholder-shown) ~ .contacts-us__label {
			top: 0;
			transform: translateY(-50%);
			font-size: 12px;
		}
	}

	&__invalid {
		position: absolute;
		z-index: 2;
		left: 12px;
		bottom: -20px;
		font-size: 13px;
		font-weight: 500;
		color: rgb(159 0 0);
	}

	&__btn {
		min-width: 150px;
		padding: 12px 24px;
		border-radius: 24px;
		background-color: transparent;
		color: #fff;
		border: 1px solid #fff;
		outline: none;
		cursor: pointer;
		transition: color 0.3s ease-in-out, background-color 0.3s ease-in-out;

		@media (hover: hover) {
			&:hover {
				background-color: #fff;
				color: #1c75bc;
			}
		}

		&:active {
			background-color: #fff;
			color: #1c75bc;
		}

		&:focus {
			outline: none;
		}
	}

	&__text {
		font-size: 10px;
		color: #d4d3d3;
	}

	.directions-dropdown {
		position: relative;
		min-width: 350px;

		&:not(.invalid-directions) {
			.directions-dropdown__btn {
				@media (hover: hover) {
					&:hover {
						background-color: #fff;

						.directions-dropdown__title {
							color: #1c75bc;
						}

						.directions-dropdown__icon {
							fill: #1c75bc;
						}
					}
				}
				&:active {
					background-color: #fff;

					.directions-dropdown__title {
						color: #1c75bc;
					}

					.directions-dropdown__icon {
						fill: #1c75bc;
					}
				}
			}
		}

		&.invalid-directions {
			.directions-dropdown__btn {
				background-color: transparent;
				border-color: rgb(159 0 0);

				.directions-dropdown__title {
					color: rgb(159 0 0);
				}

				.directions-dropdown__icon {
					fill: rgb(159 0 0);
				}
			}
		}

		&--open {
			& > .directions-dropdown__btn {
				background-color: #fff;

				& > .directions-dropdown__title {
					color: #1c75bc;
				}

				& > .directions-dropdown__icon {
					fill: #1c75bc;
					transform: rotate(180deg);
				}
			}

			& > .directions-dropdown__menu {
				padding-block: 6px;
				max-height: 550px;
			}
		}

		&__btn {
			width: 100%;
			display: flex;
			align-items: center;
			justify-content: space-between;
			padding: 6px 12px;
			border-radius: 12px;
			background-color: transparent;
			border: 2px solid #fff;
			overflow: hidden;
			transition: background-color 0.3s ease-in-out;
			cursor: pointer;
		}

		&__title {
			font-size: 14px;
			line-height: 1.8;
			color: #fff;
			transition: color 0.3s ease-in-out;
		}

		&__icon {
			width: 24px;
			height: 24px;
			fill: #fff;
			transition: transform 0.3s ease-in-out, fill 0.3s ease-in-out;
		}

		&__menu {
			position: absolute;
			display: flex;
			flex-direction: column;
			gap: 6px;
			width: 100%;
			left: 0;
			top: 40px;
			background-color: #fff;
			padding-inline: 6px;
			border-radius: 12px;
			overflow: hidden;
			max-height: 0;
			z-index: 9;
			transition: max-height 0.3s ease-in-out, padding 0.3s ease-in-out;
		}

		&__item {
			padding: 6px 12px;
			border-radius: 6px;
			font-size: 14px;
			color: #444;
			transition: color 0.3s ease-in-out, background-color 0.3s ease-in-out;
			cursor: pointer;

			&--active {
				color: #1c75bc;
			}

			@media (hover: hover) {
				&:not(.directions-dropdown__item--active):hover {
					color: #fff;
					background-color: #1c75bc;
				}
			}

			&:active {
				color: #fff;
				background-color: #1c75bc;
			}
		}
	}
}
