.schedule {
	display: flex;
	flex-direction: column;
	gap: 24px;
	align-items: center;
	padding-top: 134px;
	margin-bottom: 32px;

	&__wrapper {
		width: 100%;
		display: flex;
		flex-direction: column;
		gap: 12px;
	}

	&__top {
		width: 100%;
	}

	&-dropdown {
		position: relative;
		width: fit-content;

		&--open {
			.schedule-dropdown__btn {
				background-color: #1c75bc;
				border-color: #1c75bc;
			}

			.schedule-dropdown__title {
				color: #fff;
			}

			.schedule-dropdown__icon {
				stroke: #fff;
				transform: rotate(180deg);
			}

			.schedule-dropdown__list {
				max-height: 500px;
				border-color: #1c75bc;
				padding-block: 6px;
			}
		}

		&__btn {
			width: 100%;
			display: flex;
			align-items: center;
			justify-content: space-between;
			gap: 12px;
			background-color: transparent;
			border: 1px solid #444;
			border-radius: 12px;
			padding: 6px 12px;
			transition: border-color 0.3s ease-in-out, background-color 0.3s ease-in-out;
			cursor: pointer;

			@media screen and (max-width: 998px) {
				padding: 3px 6px;
			}

			.schedule-dropdown:not(.schedule-dropdown--open) > & {
				@media (hover: hover) {
					&:hover {
						border-color: #1c75bc;

						.schedule-dropdown__title {
							color: #1c75bc;
						}

						.schedule-dropdown__icon {
							stroke: #1c75bc;
						}
					}
				}
			}
		}

		&__title {
			color: #444;
			line-height: 1.8;
			transition: color 0.3s ease-in-out;

			@media screen and (max-width: 998px) {
				font-size: 14px;
				line-height: 1.5;
			}
		}

		&__icon {
			width: 24px;
			height: 24px;
			stroke: #444;
			stroke-width: 3px;
			fill: none;
			transition: stroke 0.3s ease-in-out, transform 0.3s ease-in-out;

			@media screen and (max-width: 998px) {
				width: 18px;
				height: 18px;
			}
		}

		&__list {
			position: absolute;
			left: 0;
			top: 46px;
			max-height: 0;
			display: flex;
			flex-direction: column;
			background-color: #fff;
			gap: 8px;
			padding: 0 6px;
			overflow: hidden;
			border: 1px solid transparent;
			border-radius: 12px;
			transition: max-height 0.3s ease-in-out, padding 0.3s ease-in-out, border-color 0.3s ease-in-out;

			@media screen and (max-width: 998px) {
				top: 35px;
			}
		}

		&__item {
			color: #444;
			font-size: 14px;
			text-wrap: nowrap;
			padding: 6px 12px;
			border-radius: 6px;
			transition: color 0.3s ease-in-out, background-color 0.3s ease-in-out;
			cursor: pointer;

			&--active {
				color: #1c75bc;
			}

			@media screen and (max-width: 998px) {
				font-size: 12px;
				padding: 3px 6px;
			}

			@media (hover: hover) {
				&:not(.schedule-dropdown__item--active) {
					&:hover {
						color: #fff;
						background-color: #1c75bc;
					}
				}
			}

			&:active {
				color: #fff;
				background-color: #1c75bc;
			}
		}
	}

	&-table {
		width: fit-content;
		display: flex;
		flex-direction: column;
		flex-grow: 1;

		&__wrapper {
			display: flex;

			&::-webkit-scrollbar {
				width: 0;
			}

			@media screen and (max-width: 998px) {
				width: 100%;
				overflow-x: scroll;
			}
		}

		&__header {
			display: flex;
			align-items: center;
			justify-content: space-between;
			border: 1px solid #94989a;
			border-top-left-radius: 24px;
			border-top-right-radius: 24px;
			background-color: #e5e9ec;
		}

		&__text,
		&__day {
			width: 100%;
			font-size: 14px;
			font-weight: 400;
			color: #444;
			text-align: center;

			@media screen and (max-width: 998px) {
				font-size: 12px;
			}
		}

		&__text {
			font-size: 13px;
			line-height: 1.5;
			color: inherit;
		}

		&__day {
			padding: 12px;
			text-wrap: nowrap;

			@media screen and (max-width: 998px) {
				padding: 6px;
			}

			&:not(:last-child) {
				border-right: 1px solid #94989a;
			}
		}

		&__body {
			border: 1px solid #94989a;
			border-top: none;
			border-bottom-left-radius: 24px;
			border-bottom-right-radius: 24px;
			overflow: hidden;
		}

		&__row {
			display: grid;
			grid-template-columns: repeat(7, 1fr);

			&:not(:last-child) {
				border-bottom: 1px solid #94989a;
			}
		}

		&__ceil {
			width: 100%;
			height: 100%;
			min-width: 120px;
			display: flex;
			flex-direction: column;
			justify-content: center;
			padding: 12px;
			color: #fff;

			@media screen and (max-width: 998px) {
				padding: 6px;
			}

			&--none {
				background-color: #1c74bc36;
			}

			&--red {
				background-color: rgba(146, 18, 18, 0.74);
			}

			&--green {
				background-color: rgba(115, 192, 48, 0.603);
			}

			&--blue {
				background-color: rgba(74, 89, 222, 0.584);
			}

			&--yellow {
				background-color: rgba(179, 179, 21, 0.956);
			}

			&--orange {
				background-color: rgba(255, 166, 0, 0.597);
			}

			&--aqua {
				background-color: rgba(22, 200, 200, 0.663);
			}

			&--bisque {
				background-color: rgba(226, 175, 112, 0.942);
			}

			&--blueviolet {
				background-color: rgba(137, 43, 226, 0.715);
			}

			&--fuchsia {
				background-color: rgba(255, 0, 255, 0.521);
			}

			&--chartreuse {
				background-color: rgb(130, 222, 37);
			}

			&--brown {
				background-color: rgb(165, 17, 17);
			}

			&--darkcyan {
				background-color: darkcyan;
			}

			&--darkgrey {
				background-color: rgb(119, 118, 118);
			}

			&--indigo {
				background-color: indigo;
			}

			&--chocolate {
				background-color: chocolate;
			}

			&--lightcoral {
				background-color: lightcoral;
			}

			&--darkslategray {
				background-color: darkslategray;
			}

			&--mediumseagreen {
				background-color: mediumseagreen;
			}

			&--khaki {
				background-color: rgb(124, 118, 59);
			}

			&--olive {
				background-color: rgb(90, 90, 6);
			}

			&--peru {
				background-color: peru;
			}

			&--mediumpurple {
				background-color: rgb(91, 76, 120);
			}

			&--antiquewhite {
				background-color: rgb(206, 185, 160);
			}

			&--dodgerblue {
				background-color: dodgerblue;
			}

			&--coral {
				background-color: rgb(243, 117, 71);
			}

			&--gold {
				background-color: gold;
			}

			&--darksalmon {
				background-color: rgb(255, 150, 115);
			}

			&--darkkhaki {
				background-color: darkkhaki;
			}

			&:not(:last-child) {
				border-right: 1px solid #94989a;
			}
		}
	}
}
