.site-map {
	display: flex;
	flex-direction: column;
	align-items: center;
	gap: 24px;
	padding-top: 134px;
	margin-bottom: 32px;

	&__content {
		width: 50%;
		background-color: #1c75bc;
		border-radius: 12px;
		padding: 24px;

		@media screen and (max-width: 1160px) {
			width: 75%;
		}

		@media screen and (max-width: 998px) {
			width: 100%;
		}
	}

	&__list {
		display: flex;
		flex-direction: column;
		gap: 12px;

		&:nth-child(n + 2) {
			padding-inline-start: 32px;

			& > .site-map__item {
				position: relative;

				&::after {
					content: "";
					position: absolute;
					width: 12px;
					left: -24px;
					top: calc(50% - 2px);
					transform: translateY(-50%);
					border: 1.5px solid #fff;
				}
			}

			& > .site-map__item > .site-map__link {
				font-size: 16px;
			}

			& > .site-map__item > .site-map__btn {
				font-size: 16px;
			}
		}
	}

	&__item {
		display: flex;
		flex-direction: column;
		gap: 12px;
	}

	&__link,
	&__btn {
		width: fit-content;
		font-size: 18px;
		font-weight: 400;
		text-align: start;
		color: #fff;
		padding-bottom: 2px;
		border-bottom: 1px solid transparent;
		transition: border-color 0.3s ease-in-out;

		@media (hover: hover) {
			&:hover {
				border-color: #fff;
			}
		}

		&:active {
			border-color: #fff;
		}
	}

	&__btn {
		background-color: transparent;
		cursor: pointer;
	}
}
