*,
*::before,
*::after {
	box-sizing: border-box;
	padding: 0;
	margin: 0;
}

html {
	font-size: 16px;
	scroll-behavior: smooth;
}

html,
body {
	position: relative;
}

body {
	max-width: 100vw;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	font-size: 1rem;
	font-family: "Montserrat", sans-serif;
	font-weight: 400;
	overflow-x: hidden;
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
}

a {
	text-decoration: none;
}

ul {
	list-style: none;
}

button {
	border: none;
	outline: none;

	&:focus {
		outline: none;
	}
}

.container {
	max-width: 1480px;
	padding-inline: 12px;
	margin: 0 auto;
}

.title {
	position: relative;
	width: fit-content;
	font-size: 26px;
	padding-bottom: 16px;
	text-align: center;

	&::after {
		content: "";
		position: absolute;
		width: 110%;
		height: 1.5px;
		left: 50%;
		bottom: 0;
		background-color: #1c75bc;
		transform: translateX(-50%);
	}
}

.bg-transparent {
	background-color: transparent !important;
}

.no-scroll {
	overflow: hidden;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
	-webkit-text-fill-color: #fff;
	-webkit-box-shadow: 0 0 0 30px #1c75bc inset !important;
}
