.teachers {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 24px;
  padding-top: 134px;
  margin-bottom: 32px;

  &__block{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 24px;
    padding-top: 24px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(3, minmax(250px, 400px));
    gap: 32px;

    @media screen and (max-width: 998px) {
      grid-template-columns: repeat(2, minmax(250px, 400px));
    }

    @media screen and (max-width: 678px) {
        grid-template-columns: repeat(1, minmax(250px, 450px));
      }
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 6px;
    padding: 24px;
    border-radius: 12px;
    overflow: hidden;
    box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px,
      rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
  }

  &__img {
    width: 250px;
    height: 250px;
    object-fit: cover;
    object-position: center;
    border-radius: 50%;
  }

  &__name {
    text-align: center;
    font-size: 20px;
    font-weight: 500;
    color: #1c75bc;
  }

  &__position {
    text-align: center;
    color: #444;
  }
}
