.directions {
	display: flex;
	flex-direction: column;
	align-items: center;
	gap: 32px;
	padding-top: 134px;
	margin-bottom: 32px;

	&__list {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		gap: 36px 144px;

		@media screen and (max-width: 998px) {
			gap: 36px;
		}

		@media screen and (max-width: 678px) {
			grid-template-columns: 1fr;
		}
	}

	&__link {
		display: flex;
		flex-direction: column;
		align-items: center;
		gap: 6px;
		color: #000;
		padding: 24px;
	}

	&__item {
		max-width: 380px;
		border-radius: 12px;
		border: 2px solid;
		box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
		transition: box-shadow 0.3s ease-in-out;

		@media (hover: hover) {
			&:hover {
				box-shadow: 0px 0 24px 0px #1c75bc;
			}
		}

		&:active {
			box-shadow: 0px 0 24px 0px #1c75bc;
		}

		&:nth-child(-n + 3) {
			border-color: #50c316;
		}

		&:nth-child(n + 4):nth-child(-n + 6) {
			border-color: #7f7f7f;
		}

		&:nth-child(n + 7):nth-child(-n + 9) {
			border-color: #e2ac00;
		}

		&:nth-child(n + 10):nth-child(-n + 12) {
			border-color: #ca1111;
		}

		&-img {
			display: block;
			width: 54px;
			height: 54px;
			object-fit: cover;
			object-position: center;
			transition: transform 0.3s ease-in-out;
		}

		&-title {
			font-size: 18px;
			font-weight: 600;
			text-align: center;
			text-transform: uppercase;
			transition: transform 0.3s ease-in-out;

			@media screen and (max-width: 998px) {
				font-size: 18px;
			}
		}

		&-description {
			text-align: center;
			line-height: 1.8;
			transition: transform 0.3s ease-in-out;

			@media screen and (max-width: 998px) {
				font-size: 14px;
			}
		}
	}
}
